import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { Link, graphql } from "gatsby"
import { LocalesContext } from '../components/locales/LocalesContextProvider'
import SEO from '../components/seo'
import '../styles/blog-tags.css'
import config from'../../gatsby-config'
const { locales } = config.siteMetadata

/**
 * Tags Template component (JSX)
 */
const Tags = ({ pageContext, data }) => {

  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { post: posti18n, allTags: i18nAllTags } = i18nData?.locales

  const { tag } = pageContext
  const { edges, totalCount } = data.allMdx
  const tagHeader = `${totalCount} ${posti18n}${
    totalCount === 1 ? "" : "s"
  }`

  return (
    <>
    <SEO
    title={tag}
    description={tagHeader}
    lang={langKey}
    pageId={tag}
    template='page tags'
  />

    <div id="content" className="container">
      <div className="pageTitle">
        <h1>{tag}</h1>
        <h2>{tagHeader}</h2>
      </div>
      <div className="content">
        <ul className="taxonomy">
          {edges.map(({ node }) => {
            const { slug } = node.fields
            const { title } = node.frontmatter
            return (
              <li key={slug}>
                <Link to={slug}>{title}</Link>
              </li>
            )
          })}
        </ul>
      </div>
      <aside id="sidebar">
        <Link to={ langKey === locales.defaultLangKey ? '/tags/' : `/${langKey}/tags`}>{i18nAllTags}</Link>
      </aside>
    </div>
    </>
  )
}

Tags.propTypes = {
  pageContext: PropTypes.shape({
    tag: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
            }),
            fields: PropTypes.shape({
              slug: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
}

export default Tags

export const pageQuery = graphql`
query($tag: String, $langKey: String) {
    allMdx(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { 
        frontmatter: { tags: { in: [$tag] } },
        fields: {langKey: {in: [$langKey]}}
      }
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
            langKey
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`